import Vue from "vue";
import Router, { RouterOptions } from "vue-router";

Vue.use(Router);

const routeConfig: RouterOptions = {
  mode: "history",
  base: qTouch.basePath + qTouch.appPathMap.openPath,
  routes: [
    {
      path: "/",
      name: "openPath",
      component: () => import("./views/MainPath.vue")
    }
  ]
};

export default routeConfig;
